@import url(https://fonts.googleapis.com/css?family=Noto+Sans);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR);
body {
  margin: 0;
  font-family: "Noto Sans", "Noto Sans KR", Sans-serif, Helvetica !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.editor_root {
  padding: 0.5rem;
  display: flex;
}

.editor_input_container {
  display: flex;
  flex-direction: column;
  width: 48.5% !important;
  height: 100%;
  margin-right: 1% !important;
}

.editor_input {
  width: 100% !important;
}

.editor_viewer_container {
  display: flex;
  flex-direction: column;
  width: 48.5% !important;
  height: 100%;
  margin-left: 1% !important;
}

.editor_viewer {
  overflow: auto;
  height: 100%;
}

.markdown_viewer {
  line-height: 1.5;
}

.markdown_ol {
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  -webkit-padding-start: 40px;
          padding-inline-start: 40px;
}

.markdown_ul {
  display: block;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  -webkit-padding-start: 40px;
          padding-inline-start: 40px;
}

.markdown_paragraph {
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
}

.markdown_emphasis {
  font-style: italic;
}

.markdown_strong {
  font-weight: bold;
}

.markdown_link:link {
  color: red;
  text-decoration: underline;
  font-weight: bold;
}

.markdown_link:visited {
  color: green;
  text-decoration: underline;
  font-weight: bold;
}

.markdown_link:hover {
  color: hotpink;
  text-decoration: underline;
  font-weight: bold;
}

.markdown_link:active {
  color: blue;
  text-decoration: underline;
  font-weight: bold;
}

.markdown_blockquote {
  padding: 0 1em;
  color: #6a737d;
  border-left: .25em solid #dfe2e5;
}

.markdown_table {
  border-spacing: 0;
  border-collapse: collapse;
}

.markdown_pre {
  background: #f6f8fa;
  border-radius: 6px;
  padding: 16px;
}

.markdown_inline_code {
  background: #f6f8fa;
  border-radius: 6px;
  padding: 4px;
}

.app_root {
  padding: 10px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

